import React, {Component} from "react"
import { Button } from "../components/buttons"
import {Input} from "../components/input"
import { Title } from "../components/title";

export class Calculator extends Component{
    constructor(){
        super(); // must call parent constructor
        this.state = {
            "question":"",
            "answer":""
        }
        
        console.log("State ", this.state);

    }
    getOutput(){
        
    }
    showOnScreen(str){

    }
    takeInput(event){
        // console.log(event.target.innerHTML);
        // this.num +=event.target.innerHTML;
        // this.showOnScreen(this.num);
        // console.log(this.num);
        const val = event.target.innerHTML;
        switch(val){
        case('='):{
                if (this.state.question!=='')
                {
                var ans='';
                    try
                    {
                        ans = eval(this.state.question);
                    }
                    catch(err)
                    {   
                        ans = "Math Error"
                    }
                    this.setState({ answer: ans , question: ''});   
                }
                break;
        }
        case 'CLEAR ALL': {
            this.setState({ question: '', answer: '' });
            break;
        }
 
        case 'CE': {
            var str = this.state.question;
            str = str.substr(0,str.length-1);
            this.setState({question: str});
            break;
        }
 
        default: {
            this.setState({...this.state, question: this.state.question += val})
            break;
        }

    }
    }
    render(){
        console.log("Render Called...");
        return (<div className = 'container'>
         <Title/>
         <div>
            <div className = 'mb-2'>
                <Input txt = 'Input' input = {this.state.question} />
                <Input txt = 'Output'input = {this.state.answer}/>
            </div>
            <div>
            <div className = 'mb-2'>
                <Button txt = '(' input={this.takeInput.bind(this)}/>
                <Button txt = ')' input={this.takeInput.bind(this)}/>
                <Button txt = '%' input={this.takeInput.bind(this)}/>
                <Button txt = '+' input={this.takeInput.bind(this)}/>
            </div >
            <div className = 'mb-2'>
                <Button txt = '7' input={this.takeInput.bind(this)}/>
                <Button txt = '8' input={this.takeInput.bind(this)}/>
                <Button txt = '9' input={this.takeInput.bind(this)}/>
                <Button txt = '/' input={this.takeInput.bind(this)}/>
            </div>
            <div className = 'mb-2'>
                <Button txt = '4' input={this.takeInput.bind(this)}/>
                <Button txt = '5' input={this.takeInput.bind(this)}/>
                <Button txt = '6' input={this.takeInput.bind(this)}/>
                <Button txt = '*' input={this.takeInput.bind(this)}/>
            </div>
            <div className = 'mb-2'>
                <Button txt = '1' input={this.takeInput.bind(this)}/>
                <Button txt = '2' input={this.takeInput.bind(this)}/>
                <Button txt = '3' input={this.takeInput.bind(this)}/>
                <Button txt = '-' input={this.takeInput.bind(this)}/>
            </div>
            <div className = 'mb-2'>
                <Button txt = '.' input={this.takeInput.bind(this)}/> 
                <Button txt = '0' input={this.takeInput.bind(this)}/> 
                <Button txt = '=' input={this.takeInput.bind(this)}/>
            </div>
            <div className = 'mb-2'>
                <Button txt = 'CLEAR ALL' input={this.takeInput.bind(this)}/> 
                <Button txt = 'CE' input={this.takeInput.bind(this)}/>
            </div>
            </div>
         </div>
          
        </div>)
    }
}





