export const Input = ({txt, input})=>{
    const mystyle = {
        width: "99%",
        padding: "12px 20px",
        margin: "8px",
        fontWeight: "bold"
      };
    return (
        <div>
            <input style={mystyle} type = 'text' readOnly placeholder = {txt} value={input}/>
        </div>
        
    )
    
}