export const Button = (props)=>{
        const mystyle = {
                minWidth: "90px",
        };
        let cl = ''
        if(props.txt.length>1){
                cl = 'btn btn-danger btn-lg mx-1'
        }
        else if(props.txt === '='){
                cl = 'btn btn-info btn-lg mx-1'
        }
        else{
                cl = 'btn btn-outline-primary btn-lg mx-1';
        }
        return (<button className = {cl} style={mystyle} onClick = {props.input}>{props.txt}</button>)
    
}